import React from "react"
import { useNavigate } from "react-router-dom"
import { useAuth } from "../../Services/AuthService/AuthService"

const LogoutButton = () => {
  const navigate = useNavigate()
  const { logout } = useAuth()  // Pouit useAuth hooku

  const handleLogout = async () => {
    logout()  // Voln funkce logout z kontextu
    // Nen poteba, AuthProvider to u dl: localStorage.removeItem("token")
    navigate("/login")
  }

  return (
      <button
          onClick={handleLogout}
          className="btn bg-danger text-white fs-5 ms-auto"
          style={{ fontWeight: "600" }}
      >
        Logout
      </button>
  )
}

export default LogoutButton