import React, { useEffect } from "react"
import { useNavigate, useLocation } from "react-router-dom"
import { useAuth } from "../../Services/AuthService/AuthService"

const OAuthCallback = () => {
  const navigate = useNavigate()
  const location = useLocation()
  const { loginWithGoogle } = useAuth()  // Zmna nzvu funkce

  useEffect(() => {
    // Extrakce token parametru z URL
    const params = new URLSearchParams(location.search)
    const idToken = params.get("id_token") || params.get("credential")

    const handleCallback = async () => {
      try {
        if (idToken) {
          await loginWithGoogle(idToken)
          console.log("Navigating to calendar")
          navigate("/calendar")
        } else {
          console.error("Chyb pihlaovac token. Navigate to login")
          navigate("/login")
        }
      } catch (error) {
        console.error("Chyba pi zpracovn callback:", error)
        navigate("/login", { state: { error: "Pihlen selhalo" } })
      }
    }

    handleCallback().catch(error => {
      console.error("Neoeten chyba v callback:", error)
      navigate("/login", { state: { error: "Neoekvan chyba" } })
    })
  }, [location, loginWithGoogle, navigate])

  return (
      <div className="oauth-callback-container">
        <h2>Probh pihlaovn...</h2>
        <p>Prosm ekejte, budete pesmrovni.</p>
      </div>
  )
}

export default OAuthCallback