import React, { createContext, useContext, useState, useEffect } from "react"
import axios from "axios"
import { jwtDecode } from "jwt-decode"
import { authService } from "../GoogleLoginService/GoogleLoginService"

// Vytvoen AuthContext
const AuthContext = createContext(undefined)

export const useAuth = () => {
  const context = useContext(AuthContext)
  if (!context) {
    throw new Error("useAuth mus bt pouit uvnit AuthProvider")
  }
  return context
}

export const AuthProvider = ({ children }) => {
  // Jednotn nzev pro token v localStorage
  const TOKEN_KEY = "auth_token"
  const EXPIRATION_KEY = "auth_expiration"

  const [token, setToken] = useState(localStorage.getItem(TOKEN_KEY))
  const [expiration, setExpiration] = useState(
      localStorage.getItem(EXPIRATION_KEY)
          ? new Date(localStorage.getItem(EXPIRATION_KEY))
          : null
  )
  const [loading, setLoading] = useState(false)
  const [userRole, setUserRole] = useState(null)

  // Inicializace role uivatele pi naten
  useEffect(() => {
    if (token) {
      const role = extractUserRole(token)
      setUserRole(role)
    }
  }, [])

  // Kontrola expirace tokenu
  useEffect(() => {
    if (token && expiration && new Date() > expiration) {
      console.log("Token vyprel")
      logout()
    }
  }, [token, expiration])

  // Pomocn funkce pro extrakci role z tokenu
  const extractUserRole = (jwtToken) => {
    if (!jwtToken) return null

    try {
      const decodedToken = jwtDecode(jwtToken)
      return decodedToken["https://schemas.microsoft.com/ws/2008/06/identity/claims/role"] || null
    } catch (error) {
      console.error("Chyba pi zskvn role:", error)
      return null
    }
  }

  // Pomocn funkce pro uloen tokenu a jeho parametr
  const saveToken = (jwtToken, tokenExpiration) => {
    setToken(jwtToken)
    console.log("Token uloen:", jwtToken)
    setExpiration(tokenExpiration)

    // Extrakce role z tokenu
    const role = extractUserRole(jwtToken)
    setUserRole(role)

    // Uloen do localStorage
    localStorage.setItem(TOKEN_KEY, jwtToken)
    console.log("Token uloen do LocalStorage:", jwtToken)
    if (tokenExpiration) {
      localStorage.setItem(EXPIRATION_KEY, tokenExpiration.toISOString())
    }
  }

  // Google pihlen
  const loginWithGoogle = async (idToken) => {
    setLoading(true)
    try {
      const result = await authService.googleLogin(idToken)

      // Oven formtu vsledku
      if (!result || !result.token) {
        throw new Error("Google pihlen neposkytlo platn token")
      }

      saveToken(result.token, result.expiration)
      return result
    } catch (error) {
      console.error("Google pihlen selhalo:", error)
      throw error
    } finally {
      setLoading(false)
    }
  }

  // Klasick pihlen pomoc uivatelskho jmna a hesla
  const loginWithCredentials = async (username, password) => {
    setLoading(true)
    try {
      // Pouit sprvnho formtu parametr a withCredentials
      const response = await axios.post(
          "https://recordsapi.runasp.net/api/Account/Jwt-login",
          {
            userName: username, // Pozor na sprvn nzev parametru!
            password: password
          },
          {
            withCredentials: true, // Dleit pro cookies
            headers: {
              'Content-Type': 'application/json',
              'Accept': 'application/json'
            }
          }
      );

      console.log("Login result:", response.data);

      const { token: jwtToken, expiration: expStr } = response.data;

      if (!jwtToken) {
        throw new Error("Pihlen neposkytlo platn token");
      }

      // Pevod expirace na Date objekt
      const tokenExpiration = expStr ? new Date(expStr) : null;

      saveToken(jwtToken, tokenExpiration);

      // DESERIALIZACE JWT DAT Z BACKENDU do kle "user" - stejn jako u Google pihlen
      const userData = authService.parseJwt(jwtToken);
      localStorage.setItem("user", JSON.stringify(userData));
      console.log("Deserializovna data z backendu: ", userData);

      return response.data;
    } catch (error) {
      console.error("Pihlen selhalo:", error);
      throw error;
    } finally {
      setLoading(false);
    }
  };
  // const loginWithCredentials = async (username, password) => {
  //   setLoading(true)
  //   try {
  //     // Zde upravte URL podle va API
  //     const response = await axios.post("https://recordsapi.runasp.net/api/Account/Jwt-login", { username, password })
  //
  //     // Pedpokldme, e API vrac objekt s token a expiration
  //     const { token: jwtToken, expiration: expStr } = response.data
  //     console.log("Login result:", response.data)
  //
  //     if (!jwtToken) {
  //       throw new Error("Pihlen neposkytlo platn token")
  //     }
  //
  //     // Pevod expirace na Date objekt
  //     const tokenExpiration = expStr ? new Date(expStr) : null
  //
  //     saveToken(jwtToken, tokenExpiration)
  //     return response.data
  //   } catch (error) {
  //     console.error("Pihlen selhalo:", error)
  //     throw error
  //   } finally {
  //     setLoading(false)
  //   }
  // }

  // Manuln nastaven tokenu (nap. po refresh strnky)
  const setAuthToken = (jwtToken, tokenExpiration = null) => {
    try {
      if (!jwtToken) {
        throw new Error("Nelze nastavit przdn token")
      }

      // Dekdovn tokenu pro oven platnosti
      const decodedToken = jwtDecode(jwtToken)

      // Pokud nen zadna expirace, zkusit ji zskat z tokenu
      if (!tokenExpiration && decodedToken.exp) {
        tokenExpiration = new Date(decodedToken.exp * 1000)
      }

      saveToken(jwtToken, tokenExpiration)
      return { token: jwtToken, decodedToken, expiration: tokenExpiration }
    } catch (error) {
      console.error("Nastaven tokenu selhalo:", error)
      throw error
    }
  }

  // Odhlen
  const logout = () => {
    setToken(null)
    setExpiration(null)
    setUserRole(null)
    localStorage.removeItem(TOKEN_KEY)
    localStorage.removeItem(EXPIRATION_KEY)
    localStorage.removeItem('auth_user')
    localStorage.removeItem('user')
    // localStorage.clear()
    console.log("Local storage po vyitn:", localStorage)  }

  // Zskn dekdovanch dat z tokenu
  const getDecodedToken = () => {
    if (!token) return null

    try {
      return jwtDecode(token)
    } catch (error) {
      console.error("Chyba pi dekdovn tokenu:", error)
      return null
    }
  }

  // Kontrola autentifikace
  const isAuthenticated = !!token && (!expiration || new Date() < expiration)

  // Generick funkce pro autentifikovan poadavky
  const authRequest = (method, url, data = null) => {
    if (!token) {
      return Promise.reject("Autentifikan token nenalezen")
    }

    const headers = {
      Authorization: `Bearer ${token}`,
    }

    const config = {
      method,
      url,
      headers,
    }

    if (data) {
      config.data = data
    }

    return axios(config)
  }

  // Pomocn metody pro jednotliv HTTP metody
  const authGet = (url) => authRequest("get", url)
  const authPost = (url, data) => authRequest("post", url, data)
  const authPut = (url, data) => authRequest("put", url, data)
  const authDelete = (url) => authRequest("delete", url)

  const contextValue = {
    isAuthenticated,
    token,
    userRole,
    loading,

    // Pihlaovac metody
    loginWithCredentials,
    loginWithGoogle,
    setAuthToken, // Pro manuln nastaven
    logout,

    // Informace o uivateli
    getDecodedToken,
    getUserRole: () => userRole,

    // Autentifikovan poadavky
    authRequest,
    authGet,
    authPost,
    authPut,
    authDelete
  }

  return (
      <AuthContext.Provider value={contextValue}>
        {children}
      </AuthContext.Provider>
  )
}

// Pomocn exporty pro zptnou kompatibilitu
export const getAuthToken = () => localStorage.getItem("auth_token")

export const getDecodedToken = () => {
  const token = getAuthToken()
  if (!token) return null

  try {
    return jwtDecode(token)
  } catch (error) {
    console.error("Chyba pi dekdovn tokenu:", error)
    return null
  }
}

export const getUserRole = () => {
  const token = getAuthToken()
  if (!token) return null

  try {
    const decodedToken = jwtDecode(token)
    return decodedToken["https://schemas.microsoft.com/ws/2008/06/identity/claims/role"] || null
  } catch (error) {
    console.error("Chyba pi zskvn role:", error)
    return null
  }
}

export const authAxios = (method, url, data = null) => {
  const token = getAuthToken()
  if (!token) {
    return Promise.reject("Autentifikan token nenalezen")
  }

  const headers = {
    Authorization: `Bearer ${token}`,
  }

  const config = {
    method,
    url,
    headers,
  }

  if (data) {
    config.data = data
  }

  return axios(config)
}
